  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

input,textarea{width:100%;display:block}

body,h1,h2,h3,h4,p,
ul[class],
ol[class],
li,
figure,figcaption,blockquote,
dl,
dd {
  margin: 0;
}

li {
    list-style: none;
}

ul {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: white;
}
